import {Navbar} from "@nextui-org/react";
import logo from '../../images/SimpleLogoWithoutBackground.svg'
import TText from "../Common/TText";
import TCollapse from "../Common/TCollapse";
import { Link, useLocation } from "react-router-dom";
import logoOnlyText from "./logoOnlyText.svg";
import useValue from "../../hooks/useValue";
import { useEffect, useRef } from "react";


function MobileHeader() {
    const titleFontCss = {
        fontSize: "27px",
        weight: 500,
        cursor: "pointer"
    };

    const clickable = {
        fontSize: "25px",
        cursor: "pointer",
        padding: "5px 10px",
        "&:hover": {
            border: "1px solid",
        }
    }

    const toggleRef = useRef(null);
    const navbarIsOpen = useValue(false);
    const location = useLocation();

    const onSelectItem = () => {
        if (!navbarIsOpen.value) {
            return;
        }
        toggleRef.current.click();
    }

    useEffect(() => {
        onSelectItem();
    }, [location.pathname])

    const collapseItems = [
        <Link to={"/pages/poster"} onClick={onSelectItem}>
            <TText light css={titleFontCss}>Афиша</TText>
        </Link>,
        <TCollapse
            title={<TText light css={titleFontCss}>Репертуар</TText>}
            content={
            <ul style={{margin: "0"}}>
                <li><TText light css={clickable}>Текущий</TText></li>
                <li><TText light css={clickable}>По Пушкинской карте</TText></li>
                <li><TText light css={clickable}>Архив</TText></li>
            </ul>}
        />,
        <TCollapse
            title={<TText light css={titleFontCss}>О театре</TText>}
            content={
            <ul style={{margin: "0"}}>
                <Link to="/pages/employees" onClick={onSelectItem}>
                    <li><TText light css={clickable}>Коллектив</TText></li>
                </Link>
                <Link to="/pages/history" onClick={onSelectItem}>
                    <li><TText light css={clickable}>История</TText></li>
                </Link>
            </ul>}
        />,
        <Link to={"/pages/faq"} onClick={onSelectItem}>
            <TText light css={titleFontCss}>Визит в театр</TText>
        </Link>,
        <Link to={"/pages/news"} onClick={onSelectItem}>
            <TText light css={titleFontCss}>Новости</TText>
        </Link>
    ];

    return (
        <Navbar disableShadow>
            <Navbar.Brand>
                <Link to="/" onClick={onSelectItem}>
                    <img src={logo} width={"30px"}/>
                </Link>
            </Navbar.Brand>
            <Navbar.Content>
                <Link to="/" onClick={onSelectItem}>
                    <img src={logoOnlyText} style={{height: "45px"}}/>
                </Link>
            </Navbar.Content>
            <Navbar.Toggle ref={toggleRef} showIn={"xl"} onChange={isOpen => navbarIsOpen.set(isOpen)}/>
            <Navbar.Collapse>
                {collapseItems.map((item, index) => (
                    <Navbar.CollapseItem
                        key={item}
                    >
                        {item}
                    </Navbar.CollapseItem>
                ))}
            </Navbar.Collapse>
        </Navbar>
    )
}

export default MobileHeader;