import { API } from "../../../api/api";

export function setMockFAQ(axiosMockAdapterInstance) {
    // current
    axiosMockAdapterInstance
        .onGet(API.frequentlyAskedQuestions)
        .reply(() => {
            return [200, [
                {id: 1, title: "Покупка билетов"},
                {id: 2, title: "Возврат билетов"},
                {id: 3, title: "Правила посещения"},
                {id: 5, title: "Подарочный сертификат"},
                {id: 6, title: "Пушкинская карта"},
            ]]
        })

    const faqStorage = {
        1: {title: "Покупка билетов", content: {"time":1685291919653,"blocks":[{"id":"F-AA-2zpy5","type":"paragraph","data":{"text":"Дорогие зрители, мы рады видеть вас на наших спектаклях! Приобрести билеты в Театр-Театр можно несколькими способами"}},{"id":"m0ToScoVPz","type":"header","data":{"text":"В театральной кассе","level":3}},{"id":"hXuqBJTrzG","type":"paragraph","data":{"text":"Касса находится в здании театра по ул. Ленина, 53, и работает ежедневно с 12:00 до 20:00, тел. +7 (342) 201-76-26. В дни утренних показов касса открывается за час до начала спектакля."}},{"id":"4rb3vXT4QH","type":"paragraph","data":{"text":"В театральной кассе вы можете забронировать билеты. Для льготных категорий граждан в кассе предоставляются скидки. С «Положением о порядке приобретения льготных (бесплатных) билетов отдельными категориями граждан» можно ознакомиться в разделе «Документы». Вернуть или обменять бумажные билеты можно только в кассе."}},{"id":"khlXQkfYKi","type":"header","data":{"text":"На сайте театра","level":3}},{"id":"yDZCYl1Wqi","type":"paragraph","data":{"text":"Чтобы купить билет на сайте театра, вам необходимо:"}},{"id":"7WfQ1cvsBC","type":"list","data":{"style":"unordered","items":["выбрать спектакль в разделе «Афиша»<br>","нажать на кнопку «Купить билет»<br>","выбрать дату и время спектакля<br>","выбрать свободные места в зале (количество билетов и общая стоимость будут показаны в соответствующей графе; выбранные места будут забронированы для вас на 10 минут, если по истечении этого времени билеты не будут оплачены, они автоматически вернутся в продажу; бронирование билетов через сайт на длительный период не осуществляется)<br>","ввести свои данные (имя и фамилия, телефон в формате +7 XXX XXX XX XX, электронная почта)<br>","оплатить заказ банковской картой Visa, Visa Electron, MasterCard, MasterCard Electronic, МИР или Пушкинской картой. Театр не хранит данные ваших банковских карт, вся платежная информация передается только по защищенным каналам. Если у вас не получается оплатить билет, попробуйте зайти с другого браузера (Google Chrome), очистить кеш или отключить приложения, которые блокируют всплывающие окна<br>","получить электронные билеты на почту, указанную при оформлении заказа. Если денежные средства были списаны, а билеты не пришли, свяжитесь, пожалуйста, с технической поддержкой билетной системы по тел. 8 (800) 505 67 91 (пн-пт с 10:00 до 19:00) или почте kassa@rambler-co.ru<br>"]}},{"id":"VrS0vIZy6T","type":"paragraph","data":{"text":"Электронные билеты не подлежат обмену в кассе на бумажные. Распечатывать электронные билеты не обязательно, достаточно показать их капельдинерам при входе в театр с экрана смартфона или планшета для сканирования штрихкода."}},{"id":"xbBvUKdGd-","type":"paragraph","data":{"text":"Если при оформлении заказа вы неверно выбрали дату или места в зале, то электронные билеты обменять нельзя, но можно оформить возврат и затем приобрести новые. Денежные средства после оформления возврата поступят на вашу карту в течение 10 банковских дней."}},{"id":"HkLvTtzHdI","type":"header","data":{"text":"На сайтах билетных агрегаторов ","level":3}},{"id":"3FFHiZPYaL","type":"paragraph","data":{"text":"www.afisha.ru и Яндекс.Афиша"}},{"id":"shRnaDDL-q","type":"header","data":{"text":"У официальных распространителей билетов Театра","level":3}},{"id":"Rx6zkfbv59","type":"paragraph","data":{"text":"Глуховой В.С., Зуевой М.Г., Москалевой Л.С., Мошиной Е.Ф., Парасенко В.И."}},{"id":"mOwLWWM_Vo","type":"warning","data":{"title": "Внимание!", "message":"Приобретая билеты на сторонних сайтах и через социальные сети у мошенников или перекупщиков, вы рискуете не только переплатить за билеты, но и не попасть в зал. Будьте бдительны."}}],"version":"2.27.0"}},
        2: {title: "Возврат билетов", content: {"time":1685291076831,"blocks":[{"id":"QZ8Q8x4qxP","type":"paragraph","data":{"text":"Возврат билетов оформляется по месту приобретения. В данном разделе Вы можете осуществить возврат билетов, купленных через сайт театра. Согласно Правилам продажи и возврата билетов зритель может получить обратно: 100% цены билета, сданного не позднее чем за 10 дней до спектакля; 50% – не позднее чем за пять дней; 30% – не позднее чем за три дня. Менее чем за три дня до спектакля стоимость билета не возвращается."}}],"version":"2.27.0"}},
        3: {title: "Правила посещения", content: {"time":1685291076831,"blocks":[{"id":"QZ8Q8x4qxP","type":"paragraph","data":{"text":"Возврат билетов оформляется по месту приобретения. В данном разделе Вы можете осуществить возврат билетов, купленных через сайт театра. Согласно Правилам продажи и возврата билетов зритель может получить обратно: 100% цены билета, сданного не позднее чем за 10 дней до спектакля; 50% – не позднее чем за пять дней; 30% – не позднее чем за три дня. Менее чем за три дня до спектакля стоимость билета не возвращается."}}],"version":"2.27.0"}},
        5: {title: "Подарочный сертификат", content: {"time":1685291076831,"blocks":[{"id":"QZ8Q8x4qxP","type":"paragraph","data":{"text":"Возврат билетов оформляется по месту приобретения. В данном разделе Вы можете осуществить возврат билетов, купленных через сайт театра. Согласно Правилам продажи и возврата билетов зритель может получить обратно: 100% цены билета, сданного не позднее чем за 10 дней до спектакля; 50% – не позднее чем за пять дней; 30% – не позднее чем за три дня. Менее чем за три дня до спектакля стоимость билета не возвращается."}}],"version":"2.27.0"}},
        6: {title: "Пушкинская карта", content: {"time":1685373112940,"blocks":[{"id":"7mjVJE1GtW","type":"paragraph","data":{"text":"<b>«Пушкинская карта»</b> — это специальная банковская карта для молодежи, которой можно расплачиваться только при покупке билетов на культурные мероприятия. С ее помощью молодые люди в возрасте от <mark class=\"cdx-marker\">14 до 22</mark> лет могут приобретать билеты в театр."}},{"id":"lzZtBX3Aik","type":"paragraph","data":{"text":"Карту можно оформить в любом отделении Почта Банка. Зарегистрировать виртуальную карту можно на портале <u class=\"cdx-underline\">«Госуслуги» в личном кабинете</u>. Деньги на счёт таких карт будет перечислять государство. 1 января 2022 года каждому владельцу карты программы «Пушкинская карта» было начислено 5000 рублей. Карта действует на всей территории России."}},{"id":"tO_AyR90-t","type":"paragraph","data":{"text":"Приглашаем всех держателей «Пушкинской карты» на спектакли театра!"}},{"id":"IgVwkwokEv","type":"image","data":{"file":{"url":"https://zaryadyehall.ru/upload/iblock/2bd/2bde83e5ae4625b723b5fd59ac1e1d76.jpg"},"caption":"","withBorder":false,"stretched":false,"withBackground":false}}],"version":"2.27.0"}}
    }

    axiosMockAdapterInstance
        .onGet(/\/api\/faq\/\d+/)
        .reply(config => {
            const match = config.url.match(/\/faq\/(\d+)/);
            const faqID = match[1];

            const result = faqStorage[faqID];
            if (result) {
                return [200, result]
            }

            return [400, "no content"]
        })
}
 