import { API } from "../../../api/api"

export function setMockHistory(axiosMockAdapterInstance) {
    // current
    axiosMockAdapterInstance
        .onGet(API.history)
        .reply(() => {
            return [200, {"time":1685317528634,"blocks":[{"id":"oJG-oSjV_T","type":"header","data":{"text":"Рождение","level":2}},{"id":"NdGRwhOxW1","type":"paragraph","data":{"text":"Армавирский театр не должен был родиться театром. Вернее, спектакли в новом здании, выстроенным купцом Мисожниковым, проводить планировалось, но все же прямым назначением новостройки была организация работы кафе-шантана, где проводились простые, но прибыльные развлечения. Правда, в 1908 году не все армавирцы стремились к такому времяпровождению и были против открытия еще одного питейного заведения. Они открыто выразили свой протест владельцу здания, и открытие театра состоялось. Актеры-гастролеры украинской труппы Прохоровича стали первыми, кто играл на новой армавирской сцене. В 1909 году сезон открыла украинская труппа Матусина, а еще через год - фарсово-опереточная группа под руководством Бродарева."}},{"id":"Jqv9WFBf6g","type":"paragraph","data":{"text":"К сожалению, качество постановок было невысоким. Армавирцы же хотели высокого искусства. И в 1911 году купец Мисожников был вынужден продать здание местному миллионеру Меснянкину, который мечтал создать труппу из армавирских актеров."}},{"id":"X0pfbok_5f","type":"paragraph","data":{"text":"Пока местные творческие силы, что называется, «зрели», три сезона под вывеской «Современный театр миниатюр» на сцене выступала украинская труппа под руководством Каганца."}},{"id":"KSv59zB8jC","type":"paragraph","data":{"text":"Но стоит вернуться на несколько лет назад. Еще в 1905-1906 гг. на основе русского, украинского и иных театральных кружков было учреждено первое армавирское общество любителей драматического искусства. У его инициаторов зрела мысль об устройстве общественного театра. Собрав деньги, они арендовали довольно удобное помещение рядом с гостиницей «Большая Московская» по Николаевскому проспекту (ныне улица Кирова, театр находился между нынешними домами №№ 54 и 56). С 14 февраля по 15 марта 1916 года «Новый театр» поставил пять спектаклей."}},{"id":"MgEpll3AUr","type":"paragraph","data":{"text":"Между «Современным театром миниатюр» и общедоступным «Новым театром» была существенная разница. Первый проводил сугубо коммерческую работу и мало заботился об идейном содержании постановок. «Новый театр» проводил просветительскую политику и установил доступные цены на билеты."}},{"id":"n5exTHDu22","type":"paragraph","data":{"text":"После революции, как только в городе была установлена советская власть, объединенные актерские силы театров создали советский «Народный театр», который был открыт 4 июня 1918 года спектаклем по пьесе А.М. Горького «На дне»."}},{"id":"PLfBJoYufK","type":"paragraph","data":{"text":"Так, к знакомой нам форме театр шел 10 лет. А новый этап в истории развития армавирской сцены начался 22 мая 1922 года. Именно в этом году театру было присвоено имя первого наркома просвещения Анатолия Васильевича Луначарского."}},{"id":"v-WNJCsmFD","type":"image","data":{"file":{"url":"https://historical-baggage.ru/static/part/7519-611.jpg"},"caption":"Анатолий Васильевич Луначарский","withBorder":false,"stretched":false,"withBackground":false}},{"id":"A75LwJwK_A","type":"header","data":{"text":"Второе десятилетие. 1918-1927","level":2}},{"id":"OKxNaJEG5K","type":"paragraph","data":{"text":"С приходом советской власти театр становится общественной трибуной. В Армавире гастролируют Вяльцева и Нежданова, Маяковский и Ирма Яунзем, балерина Гельцер. Во второй приезд балерины в 1926 году театр был переполнен, взволнованную и уставшую от выступления артистку беспрерывно вызывал новый зритель, театр дрожал от аплодисментов. В мае 1928 года здесь выступали со стихами молодые А. Жаров и И. Уткин. Армавирцы слушали лекции и поэмы «За книгу, за тетрадь», «Гармонь» и «Осенняя прелюдия»."}},{"id":"MEIrs6ZIBq","type":"quote","data":{"text":"6 октября 1928 года в городе вторично побывал нарком просвещения А.В. Луначарский. Дело в том, что окружной исполком и горсовет решили из-за дефицитности закрыть театр, но по совету А.В. Луначарского не сделали этого: «По всей республике, и по всему Северному Кавказу провинциальные театры значительно поднялись в культурном значении… Театр это огромное, имеющее большое общественное значение учреждение, и при внимательном отношении к делу 75% театров провинции поддерживать можно, а раз можно, то и нужно","caption":"говорил в интервью газете «Трудовой путь» нарком.","alignment":"left"}},{"id":"nB3uyp2TsM","type":"header","data":{"text":"1928-1937","level":2}},{"id":"Bv-c7kFf1G","type":"image","data":{"file":{"url":"https://historical-baggage.ru/static/part/20-9161.jpg"},"caption":"","withBorder":false,"stretched":false,"withBackground":false}},{"id":"TfXGh_4YEN","type":"paragraph","data":{"text":"В 30-ые годы руководителями театра были М.К. Маймулов (директор зрелищных предприятий в 1927-1934 гг.), Сальмоневич П.Г., Ранович А.С., Исагулян А., Луганский Б.П., Новиков А.Г. (главный художник). Театр живо откликался на темы коллективизации и промышленного строительства, ставил русскую и зарубежную классику. С большим успехом прошла премьера «Егора Булычева» М. Горького в постановке Н.Д. Ланко-Петровского и художника В. Катрановского. Критик П. Стогов писал в «Армавирской коммуне» от 16 апреля 1939 года: «Надо прямо сказать, что постановщик этого ответственейшего спектакля вполне справился с трудной задачей, поставленной автором. Спектакль получился цельным, трактовка образов, в основном, - правильной. Постановщик создал спектакль, полный внутренней силы, спектакль, в котором каждое глубокое по содержанию слово доходит до сознания зрителя». Заканчивает критик свою статью словами: «В общем, это интересный, хорошо продуманный, культурный спектакль, с чем можно поздравить весь коллектив театра. Видно, что поработали от души и на славу»."}},{"id":"gqHs8l-U0Y","type":"paragraph","data":{"text":"Последний предвоенный спектакль «Егора Булычева» состоялся весной 1941 года. Он был посвящен 45-летию артистической деятельности И.Д. Ланко-Петровского."}},{"id":"wjtAvEy5JS","type":"header","data":{"text":"Великая Отечественная","level":2}},{"id":"-buR0ypyOR","type":"paragraph","data":{"text":"В августе 1942 года здание драмтеатра сгорело после гитлеровской бомбардировки с воздуха. В тяжелые дни оккупации с августа 1942 года театральные постановки и показ кинофильмов прекратились. Вот как отражен эвакуационный период в книге приказов."}},{"id":"1jfiKl09Lz","type":"paragraph","data":{"text":"«2 августа 1942 года наш театр был эвакуирован в пределы Армянской ССР и 5 сентября начал свою работу в городе Ленинакане. Как известно, театр эвакуировался в трудных военных условиях, не имея возможности вывезти костюмы, парики и самое необходимое оформление и реквизит. Только благодаря энтузиазму и самоотверженной работе коллектива удалось так быстро приступить к возобновлению спектаклей». (28 января 1943 г., директор А.А. Исагулян)."}},{"id":"jvm8sixmCu","type":"paragraph","data":{"text":"«Работа нашего театра в последнее время строится главным образом на обслуживании частей РККА, высшего командного состава и гарнизонов городов. Все это вызывает необходимость еще строже, еще внимательней каждому работнику театра относится к своим обязанностям. Большинство работников прикладывает все свои силы&nbsp; и способности, чтобы качество спектаклей не только не снизилось, а наоборот улучшилось». (29 июля 1943 г., директор А.А. Исагулян)."}},{"id":"lKYQ-iFmgv","type":"paragraph","data":{"text":"За небольшой период – около года – Армавирский театр побывал и на гастролях, в частности, некоторое время труппа работала в Нахичевани."}},{"id":"-hIe0-eFPW","type":"paragraph","data":{"text":"Армавир был освобожден от полугодового рабства 23 января 1943 года. Актеры театра возвратились из эвакуации через несколько дней. Горисполком предоставил им полуразрушенное здание по улице Ленина (ныне дом № 84). Пока выделенное помещение восстанавливалось, труппа театра выполняла ответственную задачу – громить ненавистного врага на культурном фронте. Коллектив театра обслуживал воинские части и госпитали."}},{"id":"57juLbI6uP","type":"paragraph","data":{"text":"23 февраля 1943 года возрожденный из пепла городской драматический театр им. А.В. Луначарского вновь гостеприимно распахнул свои двери перед любителями театрального искусства. Премьерой нового сезона стал спектакль «Сады цветут» по пьесе В. Масса."}},{"id":"kxWoZP5DgX","type":"paragraph","data":{"text":"Окончательно из эвакуации, согласно приказам того времени, театр вернулся в июле 1943 года. Тогда были перевезены декорации, реквизит, костюмы."}},{"id":"02oCLMNM13","type":"paragraph","data":{"text":"Через год, 23 января 1944 года, в театре состоялось торжественное собрание партийных, советских и общественных организаций города, посвященное первой годовщине освобождения Армавира от немецко-фашистских захватчиков."}},{"id":"d5bU06oS3J","type":"paragraph","data":{"text":"Летом 1945 года здание театра было реконструировано и отремонтировано. Расширена и заново оборудована сцена, приподнят пол в зрительном зале, сделан гардероб и приведена в порядок отопительная система. Были приобретены новые костюмы для спектаклей и новый занавес из серебристого плюша. Шел первый послевоенный театральный сезон, в театре готовили спектакли мирного репертуара:"}},{"id":"Y4wulgY2WI","type":"paragraph","data":{"text":"«Горе от ума» А. Грибоедова,&nbsp;«День рождения» братьев Тур,&nbsp;«Егор Булычев» М. Горького,&nbsp;«Слуга двух господ» К. Гольдони."}},{"id":"XujvQlsGIT","type":"paragraph","data":{"text":"9 мая 1946 года первый послевоенный театральный сезон завершился спектаклем «Под каштанами Праги» по пьесе К. Симонова в постановке режиссера А.А. Пиковского. Спектакль был достоин Праздника Победы, он дышал живым ощущением торжества освобождения и братства между народами Чехии, Словакии и Советского Союза."}},{"id":"40KmK7IQ_p","type":"header","data":{"text":"1948-1967","level":2}},{"id":"echHuAUBM0","type":"image","data":{"file":{"url":"https://historical-baggage.ru/static/part/6170-7213.jpg"},"caption":"","withBorder":false,"stretched":false,"withBackground":false}},{"id":"ibbMF0N-Ah","type":"paragraph","data":{"text":"Летом 1948 года Армавирский театр отправился на гастроли в город Ленинакан, город, который дал труппе защиту и кров во время войны. Кроме того, актеры успешно выступили с новыми спектаклями в Кутаиси."}},{"id":"A8lDn17go_","type":"paragraph","data":{"text":"В конце 1953 года состоялась яркая премьера спектакля «Анджело» по произведению А. Пушкина. А в 1954 на сцене показывались «Бедность не порок» А. Островского и «Дядя Ваня» А. Чехова. Именно в 50-ые была заложена традиция выпуска в год порядка 7-10 спектаклей."}},{"id":"awtyOHnraE","type":"paragraph","data":{"text":"Начался новый этап. В 51-м сезоне (59-60 гг.) вышла комедия «Бешеные деньги» А. Островского, в постановке режиссера Л.А. Яшининой. А также спектакли «Беспокойная старость» («Депутат Балтики») по пьесе Л. Рахманова, «Семья» И. Попова, о годах юности Володи Ульянова, «Собака на сене» Лопе де Вега. И в современной советской драматургии, и в русской и зарубежной классике, и в спектаклях для детей затрагивались острые проблемы современности."}},{"id":"0xPYKf_0Z1","type":"paragraph","data":{"text":"Конец 1964 года ознаменовался крупным событием – труппа открыла сезон в обновленном здании. Об этом говорится и в приказе от 6 ноября 1964 года, подписанном директором театра Ждановичем."}},{"id":"35N0y1RApy","type":"paragraph","data":{"text":"Театральный сезон 65-66 гг. зажег новую звезду."}},{"id":"U8somEL3F8","type":"paragraph","data":{"text":"Состоялась премьера спектакля «Именем революции» по пьесе М. Шатрова, в котором образ Ленина создал актер Лев Леонидов. Это был огромный успех и театра, и артиста. К весне 66 года спектакль прошел сто восемь раз."}},{"id":"Xu_9Z_fJOw","type":"paragraph","data":{"text":"А в 66-м году состоялась премьера спектакля «Покушение на Прометея» по пьесе В. Усланова - и здесь образ вождя, созданный Леонидовым, получил самую высокую оценку зрителей и театральной критики."}},{"id":"UslwZasMPW","type":"paragraph","data":{"text":"В этот период на сцене с увлечением играли актеры Л. Леонидов, Г. Прошин, С. Шевяков, Ю. Канн, Г. Ковалев, С. Галанин, Б. Ульянкин, Л. Романов, Л. Чугурян, засл. артист РСФСР Г. Градов."}},{"id":"R11YndApEy","type":"paragraph","data":{"text":"В 1967 году страна готовилась торжественно отметить 50-летие Революции. Театр открыл сезон премьерой спектакля «Кочубей», героической драмой о легендарном сыне Кубани, герое гражданской войны. Роль Ивана Кочубея воплотил на сцене Ю. Канн. Роль Натальи, сестры милосердия, исполнила засл. артистка ЧуАССР А. Камардина."}},{"id":"8Q8HOp4EUH","type":"header","data":{"text":"1968-1977","level":2}},{"id":"6Cfx--DeVz","type":"paragraph","data":{"text":"В 1968 года армавирцы увидели на сцене своего театра драму «Последние» М. Горького, а также историческую эпопею «Агент его величества». Главные роли в обеих постановках сыграли засл. артистка ЧуАССР А. Камардина, засл. артист РСФСР Г. Градов."}},{"id":"fkV8qcFV-f","type":"paragraph","data":{"text":"Сезон 1969-70 гг. посвящался 100-летию со дня рождения В.И. Ленина. Театр, носящий имя А. Луначарского, обратился к его пьесе «Яд»."}},{"id":"pyQK9RKrkq","type":"paragraph","data":{"text":"Кроме того, были новые спектакли:"}},{"id":"nJPzJGCmQt","type":"list","data":{"style":"unordered","items":["«Жаркое лето в Берлине» Д. Кюсака,"]}},{"id":"AsZjkileH0","type":"list","data":{"style":"unordered","items":["«Память пани Близны» (новая пьеса армавирца Б. Каспарова),"]}},{"id":"il8TD7wrvd","type":"list","data":{"style":"unordered","items":["«Однажды в новогоднюю ночь» Э. Брагинского и Э. Рязанова."]}},{"id":"dSjz1odnqg","type":"paragraph","data":{"text":"Режиссуру представляли А.В. Бондаренко, К.А. Угаров, В.Н. Жданович и выпускница училища им. Щукина И.К. Карелина."}},{"id":"dunTk5hhKo","type":"paragraph","data":{"text":"В этот период пополнилась и труппа. На службу в театр поступили артисты: В. Слинько, А. Корнилов, С. Кутов, а также выпускники театральных училищ С. Бахмут, В. Лопатина, Т. Мельник, С. Догадкин. Пришел в театр новый главный художник Ю. Савин."}},{"id":"48jeoROmdM","type":"paragraph","data":{"text":"В 1972 году театр первым в стране осуществил новую постановку «Зуб дракона» по пьесе писателя-земляка Б. Каспарова."}},{"id":"uINx8mbef0","type":"paragraph","data":{"text":"Победе посвящается"}},{"id":"75C5-2imYU","type":"paragraph","data":{"text":"В канун праздника Победы в 1972 году на армавирской сцене свет увидела инсценировка по повести Б. Васильева «А зори здесь тихие». Как известно, впоследствии по этой повести спектакли поставили многие театры, а на экраны страны вышел художественный фильм."}},{"id":"TeYjZzV3fW","type":"paragraph","data":{"text":"В целом, постановки на военную тематику в то время были репертуарообразующими. Даты Октябрьской революции и Победы в ВОВ отмечались новыми спектаклями ежегодно."}},{"id":"PeE1SFt6j7","type":"header","data":{"text":"«Аджимушкай»","level":2}},{"id":"_m-eUyoC0j","type":"paragraph","data":{"text":"По спецзаказу театра лауреат Литературной премии Министерства Обороны СССР, писатель Н.И. Камбулов создал пьесу «Аджимушкай» - о героических защитниках города Керчи в годы Великой Отечественной войны. Она была с большим успехом представлена коллективом театра в канун 30-летия Победы. Режиссер-постановщик – Л.Я. Каменецкий. За этот спектакль Армавирский Государственный театр драмы имени А.В. Луначарского, единственный из городских театров РСФСР, был удостоен Диплома Всесоюзного смотра-конкурса театров республики, посвященного 30-летию Победы. По этому поводу народный артист РСФСР А. Каплер писал в газете «Советская культура»: «Армавирцы привезли свой спектакль в город Керчь, на место событий. В конце спектакля, после того, как несколько героев, оставшихся в живых, идут на последний штурм, на верную смерть, со сцены звучали стихи Ильи Сельвинского:"}},{"id":"HfARn-DsuZ","type":"paragraph","data":{"text":"«Кто всхлипывает тут? Слеза мужскаяЗдесь может прозвучать кощунством… Встать!Страна велит нам почести воздатьВеликим мертвецам Аджимушкая!...»"}},{"id":"H6J9dbf-Q5","type":"paragraph","data":{"text":"И встали все, как один, зрители, и молча стояли с залитыми слезами лицами. И долго никто не смел нарушить эту тишину аплодисментами… Я стоял среди этих людей… Я просто хочу поклониться и поблагодарить театр, сумевший так достойно отметить великий подвиг, бессмертный подвиг защитников Аджимушкая»."}},{"id":"M2myukSHUn","type":"paragraph","data":{"text":"Минувшее десятилетие было отмечено постановками разных жанров. На армавирских подмостках шли"}},{"id":"-jxxxDdOO9","type":"paragraph","data":{"text":"«Голубые олени» украинского драматурга А. Коломийца,&nbsp;«Взлетная полоса» по пьесе Э. Тобелкина,&nbsp;«Невский для прогулок» ленинградца Л. Назарова,&nbsp;комедия «Луговая, дом 24» Д. Медведенко,&nbsp;«Соломенная сторожка» Ю. Эдлиса."}},{"id":"JwyHfs68T_","type":"paragraph","data":{"text":"Классическая драматургия представлена комедией&nbsp;«Забавный случай» К. Гольдони,&nbsp;драмой «Мария Тюдор» В. Гюго,&nbsp;«Из-за чести» по пьесе классика армянской литературы А. Ширванзаде."}},{"id":"RGrB9y9Bjq","type":"paragraph","data":{"text":"К новому 1977 году театр подготовил сказку «Волшебная лампа Аладдина», которую художественный совет назвал самой изящной сказкой минувших сезонов."}},{"id":"WSdq3BYPL9","type":"paragraph","data":{"text":"В 1974 году труппа пополнилась группой молодых актеров. Так Армавир узнал Т. Весеньеву и Ю. Берсенева, С. Скрынника, Г. Кустова и В. Сударева."}},{"id":"oLOk4yvPyS","type":"paragraph","data":{"text":"В феврале 1975 года прошел бенефис Георгия Александровича Прошина, ведущего актера, у которого было сразу два юбилея – 60-летие со дня рождения и 40-летие творческой деятельности."}},{"id":"XI6uErfZnO","type":"paragraph","data":{"text":"А 8 июня 1977 года артисту Льву Борисовичу Леонидову Министерством культуры РСФСР было присвоено высокое и почетное звание заслуженного артиста РСФСР, за многолетнее успешное воплощение на сцене Армавирского государственного театра драмы им. А. Луначарского образа вождя мирового пролетариата Владимира Ильича Ленина."}},{"id":"BvimQd0U75","type":"header","data":{"text":"1978-1987","level":2}},{"id":"2FxFhP5-UM","type":"paragraph","data":{"text":"В юбилейный, 70-ый сезон на армавирской сцене имела успех героическая драма «Гибель эскадры» А. Корнейчука (режиссер-постановщик Л.Я. Каменецкий, художник-постановщик Г.А. Прошин). Громкими овациями зрители приветствовали постановки:&nbsp;«Человек на асфальте» Л. Самойлова и Н. Вирты,&nbsp;«Самая счастливая» К. Володарского,&nbsp;«Не стреляйте в белых лебедей» Б. Васильева."}},{"id":"IV85UywqcV","type":"paragraph","data":{"text":"В конце 70-х с шумным успехом шел спектакль «Царь Федор Иоаннович», поставленный режиссером Л. Каменецким и художником В. Маковым."}},{"id":"bgAJmsMwOj","type":"paragraph","data":{"text":"Тогда же закладывается новая традиция – проведение недель «Театр – детям и юношеству»."}},{"id":"0ddjYfnJJK","type":"paragraph","data":{"text":"«Горсть бриллиантов» А. Орехова,&nbsp;«Малыш и Карлсон, который живет на крыше» по А. Линдгрен,&nbsp;«Веселый тракт» В. Васильева – каждую постановку посмотрели более 4 тысяч школьников."}},{"id":"dnMAR6pTQD","type":"header","data":{"text":"Гастрольные туры","level":2}},{"id":"xQFZn1pV_H","type":"paragraph","data":{"text":"Из года в год Армавирский театр совершает гастрольные поездки по городам и селам края, Черноморскому побережью, в различные уголки необъятной страны. Труппа побывала в Махачкале и Керчи, Дербенте и Изербаше, Каспийске и Буйнакске, Анапе, Кизляре, Геленджике и Туапсе. Запоминающимися получились гастроли в Кировоградскую область Украины."}},{"id":"7-6wC_mXJJ","type":"paragraph","data":{"text":"В советские времена актеры и режиссеры, художники и работники технических цехов – желанные гости на предприятиях, стройках и в учебных заведениях, они встречаются со своими зрителями в цехах и аудиториях, знакомят трудящихся с искусством и дальнейшими творческими планами."}},{"id":"Ygxw3W6PRq","type":"paragraph","data":{"text":"Жизнь театра украсили своим искусством режиссеры Н. Карелина, Л. Подберезин, Г. Прошин. Художественными руководителями театра были Л. Каменецкий, В. Петрашевич. Среди громких постановок –"}},{"id":"-5uR100rFV","type":"paragraph","data":{"text":"«Власть тьмы» Л. Толстого,&nbsp;«Энергичные люди» В. Шукшина,&nbsp;«Восемь любящих женщин» – трагический фарс Р. Тома,&nbsp;«Свои люди – сочтемся!» А.Н. Островского,&nbsp;«Невольницы» А.Н. Островского."}},{"id":"moO5wTZagO","type":"paragraph","data":{"text":"Пришел в театр новый художественный руководитель А.Х. Курашинов. Имея больший опыт работы в театрах края, получив специальное образование в Ленинграде, он сумел в короткий срок мобилизовать творческие силы труппы. Прежде всего, это сказалось на качестве репертуара."}},{"id":"wFpu-uysIC","type":"paragraph","data":{"text":"Режиссер В. Бартосик и художник В. Маков ставят спектакль о советских людях в годы минувшей войны по пьесе В. Кондратьева «Отпуск по ранению». Постановка оказалась настолько значительной, что актеры Б. Бровченко (лейтенант Володька) и Л. Хачикян (вдова) стали одними из победителей I тура краевого конкурса лучших актерских работ, посвященного 60-летию образования СССР. Лирический спектакль о вечном стремлении человека к прекрасному – «Тема с вариациями», по пьесе С. Алешина, был отмечен блестящей работой режиссера Н. Карелиной и актеров А. Гельбета, Н. Вершининой, Б. Бровченко."}},{"id":"YNRncSLBw5","type":"paragraph","data":{"text":"Параллельно на сцене театра созидает режиссер А. Рубан в творческом тандеме с художником Краснодарского театра оперетты М. Цаур."}},{"id":"VXY7cDbNWl","type":"paragraph","data":{"text":"А вот и репертуар тех лет:"}},{"id":"uybTRoQCPc","type":"paragraph","data":{"text":"«Гибель эскадры» А. Корнейчука,&nbsp;«Бесприданница» А. Островского,&nbsp;«Бравый солдат Швейк» по Я. Гашеку,&nbsp;«Превышение власти» Г. Черных,&nbsp;«Тревога» В. Петрашкевича,&nbsp;«Репетитор» Г. Полонского,&nbsp;«Гнездо глухаря» В. Розова,&nbsp;«С любовью не шутят» П. Кальдерона,&nbsp;«Феномены» Г. Горина,&nbsp;«Святой и грешный» М. Варфоломеева,&nbsp;«Филумена Мартуране» Э де Филиппо,&nbsp;«Трамвай «Желание» Т. Уильямса,&nbsp;«Кот в сапогах» С. Прокофьева и Г. &nbsp;Сангир,&nbsp;«Отпуск крокодила Гены» Э. Успенского и Р. Качанова,&nbsp;«Волк и три поросенка» С. Михалкова,&nbsp;«Самозванец» Л. Корсунского,&nbsp;«Маленькая фея» В. Рабадана,&nbsp;«Происшествие в стране Мульти-пульти» А. Хайта и А. Левенбрука,&nbsp;«Сомбреро» С. Михалкова,&nbsp;«Два клена» Е. Шварца,&nbsp;«Снежная королева» Е. Шварца, который оказался наиболее значительной постановкой Ю. Юхименко."}},{"id":"SQTGRoWqOC","type":"paragraph","data":{"text":"К 60-летию комсомола выпускается спектакль по воспоминаниям участников ВОВ «Зарево над волнами». В нем были заняты все работники театра."}},{"id":"pgjId8hFZj","type":"paragraph","data":{"text":"Спектакль А. Курашинова «Чинарский манифест», А. Чхаидзе, театральные критики, пресса и зрители признали значительной творческой удачей театра. Постановка богата удачными актерскими работами:&nbsp;это 18-летний председатель колхоза Бадри Ломидзе – В. Вершинин,&nbsp;его соратник и подруга Ямзе – Н. Вершинина,&nbsp;отец Бадри – А. Гельбет,&nbsp;секретарь райкома КПСС – А. Курашинов,&nbsp;колхозник Индико – В. Зубенко."}},{"id":"kSBI0Dcg7k","type":"paragraph","data":{"text":"В середине 80-х у армавирского театра – переломный период. Меняется руководство и начинается долгий капитальный ремонт. Именно в это время, в 1984 году, директором театра становится Александр Хоренович Абелян."}},{"id":"XmKlLdyFiv","type":"paragraph","data":{"text":"Тридцатилетний специалист к удивлению многих смог повести театральный корабль верным курсом. Во многом, благодаря его усилиям, армавирцы гордятся своим театром сегодня. В настоящее время Александр Хоренович – член Союза театральных деятелей РФ, член ревизионной комиссии краевой организации Союза театральных деятелей РФ. Он всегда в курсе достижений российского театрального искусства. Творческий коллектив под его руководством идет в ногу со временем."}},{"id":"aCNPWqGd67","type":"paragraph","data":{"text":"Как правило, репертуар каждого нового сезона пополняют 7-8 постановок. Ежемесячно на сцене армавирского театра драмы показывается порядка 26 спектаклей. В год, соответственно, около 200 спектаклей."}},{"id":"cx0YiROhI8","type":"paragraph","data":{"text":"В 1999 года в театр пришел новый главный режиссер Юрий Алексеевич Ковалев. Опытный, с массой творческих планов, он сразу же поставил на армавирской сцене пять новых спектаклей. «Красавец-мужчина» по пьесе Н. Островского на фестивале «Кубань театральная-99» получил приз «Надежда» за режиссуру спектакля. Ученик народного артиста, лауреата государственных премий Л.А. Додина, воспитанный в духе традиций ленинградской театральной школы, он смог быстро понять пристрастия армавирских зрителей. За девять сезонов им поставлено около 40 спектаклей."}},{"id":"wkxQJUox63","type":"paragraph","data":{"text":"В репертуар вошли:&nbsp;«Трактирщица», К. Гальдони;&nbsp;«Вечно живые», В. Розов;&nbsp;«Иванов», А. Чехов; &nbsp;«Утиная охота», А. Вампилов; «Коварство и любовь», Ф. Шиллер;&nbsp;«Зойкина квартира», М. Булгаков;&nbsp;«С любовью не шутят», П. Каледерон;&nbsp;«Правда – хорошо, а счастье – лучше», А. Островский;&nbsp;«Трамвай «Желание», Т. Уильямс;&nbsp;«Лекарь поневоле», Ж.-Б. Мольер."}},{"id":"8stxAS7XGo","type":"paragraph","data":{"text":"Спектакль «Месяц в деревне» по произведению И. Тургенева был удостоен приза за оригинальное прочтение русской классики. В целях патриотического воспитания молодых людей к 60-летию Победы Армавирский театр выпустил спектакль «Так и будет» по пьесе К. Симонова в постановке Ю.А. Ковалева. Большое внимание режиссер, заслуженный деятель искусств Кубани, Юрий Ковлев уделяет работе с детьми. Для них он поставил красивые добрые сказки: «Хрустальный башмачок», Т. Габбе; «Спящая красавица», Ч. Уэй; «Винни-Пух», А. Милн, «Волшебная лампа Аладдина», Д. Краснов."}},{"id":"d1oWl2wlk_","type":"paragraph","data":{"text":"Сегодняшняя труппа театра – живой актерский синтез заслуженных артистов Кубани и России, мастеров сцены и молодых дарований. Всего – порядка двадцати человек. И играют все. Самоотдача, брызжущая в зал энергия, блестящая игра и, конечно, незаурядный талант армавирских актеров создают интригу у публики, направляют и подталкивают вновь и вновь приходить в театр."}},{"id":"oRrUun0wLe","type":"paragraph","data":{"text":"Зрители идут семьями, парами, с детьми и друзьями. Идут на новые постановки, дабы увидеть свежий режиссерский замысел, интересуются сюжетами и, безусловно, приходят на любимых артистов."}},{"id":"6FF9bOVVBt","type":"paragraph","data":{"text":"Влюбленному зрителю не надо сверяться с программой – он узнает своих любимцев даже в самом замысловатом гриме, видит в каждой талантливой роли."}},{"id":"EGAQtc4A21","type":"paragraph","data":{"text":"Минувшее десятилетие, пролетевшее как птица, вновь вернуло театру его силу и мощь. Завершился серьезный этап перемен в государстве, ломавший не только строй, но города, предприятия, людские судьбы. Но тем и сильна Россия, что в трудностях ее человек лишь крепнет духом. Выстоял и армавирский театр, причем, сохранив за собой и громкое имя, и аудиторию."}},{"id":"9vj8BKKkao","type":"paragraph","data":{"text":"По-прежнему обласканная зрителями, идет на сцене классика – русская и зарубежная. В репертуаре – постановки по произведениям Островского, Чехова, Шекспира, Пушкина. А в постановочных планах – Тургенев, Шиллер, Мольер."}},{"id":"PfCUbTBWwJ","type":"paragraph","data":{"text":"С интересом ожидают школьники и студенты выхода спектаклей о молодежи. Пусть не всегда с серьезными выражениями лиц, пусть с шутками, прикрывающими юношескую застенчивость, но ребята осознают, что используют редкую возможность посмотреть на себя со стороны. Поддерживает их живой интерес «семейная суббота» - своеобразная форма пресс-конференции актеров и зрителей."}},{"id":"Gr1eMeviP4","type":"paragraph","data":{"text":"На особом положении в армавирском театре находится сказка. Ведь с нее начинается воспитание маленького зрителя. Через сказку он впервые говорит театру «Здравствуй!». И театр отвечает: «Смотри, юный друг. Учись»."}},{"id":"GSNh5qC7w1","type":"paragraph","data":{"text":"Уже целый век армавирская сцена собирает яркие эмоции, благодарные и восторженный аплодисменты. Без сомнения, тот, кто родился в Армавире, кто живет, кто гостит здесь – обязательно бывал в городском театре."}},{"id":"neBotw__TK","type":"paragraph","data":{"text":"Дорогой зритель, приди в театр немного раньше, когда он в ожидании публики, пока еще не гудит возбужденная предстоящим зрелищем публика. Пусть твои шаги разнесет по прозрачному холлу старое эхо. Пусть зеркала и начищенный до блеска розовый мрамор отразят твое предчувствие, твое волнение. Вдохни запах зала. Может, повезет, и ты увидишь первый блик на тяжелом занавесе, который рождается задолго до начала представления. Замри. Прислушайся. Затаи дыхание. Почувствуй. Ведь где-то здесь когда-то прозвенели и твои первые аплодисменты. Запомни этот момент. И знай, что сегодня ты испытал момент, который будет яркой вспышкой вспоминаться всю жизнь, который называется счастье. Сегодня театр разговаривал с тобой."}},{"id":"k7aRma96Cq","type":"paragraph","data":{"text":"<i><mark class=\"cdx-marker\">Елена Пачина, руководитель литературно-драматической части.</mark></i>"}},{"id":"E0SGz01YxT","type":"paragraph","data":{"text":"<i><mark class=\"cdx-marker\">Использованы материалы исторической справки А. Поветкина, зав. лит. частью театра и О. Веровенко, краеведа, от 25 ноября 1982г.</mark></i>"}}],"version":"2.27.0"}]
        })
}